import React from 'react';
import {Link} from 'react-router-dom';

class HomePage extends React.Component{
    constructor(props){
        super(props)
        this.state={
        }
    }

    render(){
        return(
         <div>
               <div className="text-center" style={{marginTop:"2.5%",marginLeft:"5%",width:"20rem"}}>
               <div style={{fontSize:"12px",fontFamily:"sans-serif"}}>
               <h5>基础功能</h5>
               <div><Link to="/adduser" style={{textDecoration:"none"}}>添加用户</Link></div>
               <div><Link to="/addorganization" style={{textDecoration:"none"}}>添加组织</Link></div>
               <div><Link to="/userrolelist" style={{textDecoration:"none"}}>修改用户角色</Link></div>
               <div><Link to="/roleauthforuser" style={{textDecoration:"none"}}>修改角色权限</Link></div>
               </div>
               </div>
               <div className="text-center" style={{marginTop:"2.5%",marginLeft:"5%",width:"20rem"}}>
               <div style={{marginTop:"2.5%",fontSize:"12px",fontFamily:"sans-serif",color:"#666669"}}>
                <h5>其他功能</h5>
                <div><Link to="/addrole" style={{textDecoration:"none",fontSize:"12px"}}>添加角色</Link></div>
                <div><Link to="/userlist" style={{textDecoration:"none",fontSize:"12px"}}>修改用户信息</Link></div>
               </div>
               </div>
         </div>
        )
    }
}

export default HomePage